import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { IonicApp, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { TrendingPageModule } from '../pages/trending/trending.module';

import { ClipsService } from './services/clips.service';
import { GamesService } from './services/games.service';
import { UsersService } from './services/users.service';
import { MobileAuthenticationService } from './services/mobile-auth.service';
import { LocalStorageService } from './services/localStorage.service';
import { LoaderService } from './services/loader.service';
import { ToastService } from './services/toast.service';
import { RestService } from './services/rest.service';
import { IonicStorageModule } from '@ionic/storage';
import { AbstractAuthenticationService } from './services/abstract-auth.service';
import { environment } from '../../environments/environment';
import { WebAuthenticationService } from './services/web-auth.service';
import { CommonModule } from '@angular/common';
import { HighlightsService } from './services/highlights.service';
import { HighlightsPageModule } from '../pages/highlights/highlights.module';
import { CreateHighlightPageModule } from '../pages/createhighlight/createhighlight.module';
import { AddHighlightClipPageModule } from '../pages/addhighlightclip/addhighlightclip.module';
import { LoggingService } from './services/logging.service';
import { AuthoritiesService } from './services/authorities.service';
import { AuthoritiesModelService } from './services/authorities.model.service';
import { ChannelsPageModule } from '../pages/channels/channels.module';
import { ChannelsService } from './services/channels.service';
import { CurrentChannelStoreService } from './services/current-channel.store.service';
import { FeedPageModule } from '../pages/feed/feed.module';
import { FeedService } from './services/feed.service';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    CommonModule,
    IonicStorageModule.forRoot(),
    IonicModule.forRoot(MyApp, {
      backButtonText: '',
      backButtonIcon: 'ios-arrow-back',
      iconMode: 'md'
    }),
    AddHighlightClipPageModule,
    TrendingPageModule,
    HighlightsPageModule,
    CreateHighlightPageModule,
    ChannelsPageModule,
    FeedPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    {
      provide: AbstractAuthenticationService,
      useClass: environment.mobile ? MobileAuthenticationService : WebAuthenticationService
    },
    AuthoritiesModelService,
    AuthoritiesService,
    ChannelsService,
    ClipsService,
    FeedService,
    GamesService,
    HighlightsService,
    LoaderService,
    LoggingService,
    LocalStorageService,
    RestService,
    ToastService,
    UsersService,
    StatusBar,
    SplashScreen,

    /* Stores */
    CurrentChannelStoreService
  ]
})
export class AppModule {}



