import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { TrendingClips } from '../models/trending-clips';
import { RestService } from './rest.service';
import { AbstractAuthenticationService } from './abstract-auth.service';

@Injectable()
export class ClipsService {
  constructor(
    private authService: AbstractAuthenticationService,
    private restService: RestService) {}

    async addComment(slug: string, comment: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/${slug}/comments`;

      return this.restService.postProtected(url, authorization, JSON.stringify({"comment": comment}));
    }

    async getComments(slug: string): Promise<any> {
      let url = environment.apiAddress + `/api/v1/clips/${slug}/comments`;

      return this.restService.get(url);
    }

    async getPublicInfo(slug: string): Promise<any> {
      let url = environment.apiAddress + `/api/v1/clips/${slug}/info`;

      return this.restService.get(url);
    }

    async getBySlug(slug: string): Promise<any> {
      let url = environment.apiAddress + '/api/v1/clips/slug/' + slug;

      return this.restService.get(url);
    }

    async getFavorites(): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + '/api/v1/protected/clips/favorites';

      return this.restService.getProtected(url, authorization);
    }

    async getUserInfo(slug: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/userinfo`;

      return this.restService.getProtected(url, authorization);
    }

    async addFavorite(slug: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/favorite`;

      return this.restService.postProtected(url, authorization, null);
    }

    async removeFavorite(slug: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/unfavorite`;

      return this.restService.postProtected(url, authorization, null);
    }

    async like(slug: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/${slug}/like`;

      return this.restService.postProtected(url, authorization, null);
    }

    async unlike(slug: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/${slug}/unlike`;

      return this.restService.postProtected(url, authorization, null);
    }

    async getTrendingInitial(period: string, language: string): Promise<TrendingClips> {
      return this.getTrending(undefined, period, language);
    }

    async getTrending(cursor: string|undefined, period: string, language: string): Promise<TrendingClips> {
      let url = environment.apiAddress + `/api/v1/clips/trending?period=${period}&language=${language}`;

      if (cursor) {
        url += `&cursor=${cursor}`;
      }

      return this.restService.get(url);
    }

    async getTrendingByChannel(channel: string, cursor: string|undefined, period: string): Promise<TrendingClips> {
      let url = environment.apiAddress + `/api/v1/clips/trending/channel/${channel}?period=${period}`;

      if (cursor) {
        url += `&cursor=${cursor}`;
      }

      return this.restService.get(url);
    }

    async getTrendingByGame(game: string, cursor: string|undefined, period: string, language: string): Promise<TrendingClips> {
      let url = environment.apiAddress + `/api/v1/clips/trending/game/${game}?period=${period}&language=${language}`;

      if (cursor) {
        url += `&cursor=${cursor}`;
      }

      return this.restService.get(url);
    }
  }


