import * as Promise from 'bluebird';
import * as unirest from 'unirest';

export class RestService {
  delete(url: string, authorization: string): Promise<any> {
    return new Promise(function(resolve, reject) {
      unirest.delete(url)
      .headers({'Accept': 'application/json', 'Content-Type': 'application/json', 'Authorization': authorization})
      .send()
      .end(function(resp) {
        _unwrapResponse(resp, resolve, reject);
      });
    });
  }

  get(url: string): Promise<any> {
    return new Promise(function(resolve, reject) {
       unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {
          _unwrapResponse(resp, resolve, reject);
        });
    });
  }

  getProtected(url: string, authorization: string): Promise<any> {
    return new Promise(function(resolve, reject) {
      unirest.get(url)
      .headers({'Accept': 'application/json', 'Authorization': authorization})
      .send()
      .end(function(resp) {
        _unwrapResponse(resp, resolve, reject);
      });
    });
  }

  post(url: string, obj: any): Promise<any> {
    return new Promise(function(resolve, reject) {
      unirest.post(url)
      .headers({'Accept': 'application/json', 'Content-Type': 'application/json'})
      .send(obj)
      .end(function(resp) {
        _unwrapResponse(resp, resolve, reject);
      });
    });
  }

  postProtected(url: string, authorization: string, obj: any): Promise<any> {
    return new Promise(function(resolve, reject) {
      unirest.post(url)
      .headers({'Accept': 'application/json', 'Content-Type': 'application/json', 'Authorization': authorization})
      .send(obj)
      .end(function(resp) {
        _unwrapResponse(resp, resolve, reject);
      });
    });
  }
}

const _unwrapResponse = (response: any, resolve: Function, reject: Function) => {
  if (response.code === 404) {
    reject(new Error('HTTP URL not found.'));
  } else if (response.code === 401) {
    reject(new Error('User is not authorized.'));
  }
  else if (response && response.body) {
    let body;

    if (typeof response.body === 'string') {
      body = JSON.parse(response.body);
    } else {
      body = response.body;
    }

    if (body.success !== undefined && body.success === true) {
      if (typeof body.response === 'undefined') {
        body.response = {};
      }
      resolve(body.response);
    } else {
      reject(new Error(body.message || 'The response contains no message.'));
    }
  } else {
    reject(new Error('The response contains no body.'));
  }
};


