// src/services/auth.service.ts
import { Injectable, NgZone } from '@angular/core';

// Import AUTH_CONFIG, Auth0Cordova, and auth0.js
import { AUTH_CONFIG } from './auth.config';
import Auth0Cordova from '@auth0/cordova';
import * as auth0 from 'auth0-js';

import { environment } from '../../../environments/environment';
import { LocalStorageService } from './localStorage.service';
import { RestService } from './rest.service';
import { AbstractAuthenticationService } from './abstract-auth.service';

const restService: RestService = new RestService();

@Injectable()
export class MobileAuthenticationService implements AbstractAuthenticationService {
  Auth0 = new auth0.WebAuth(AUTH_CONFIG);
  Client = new Auth0Cordova(AUTH_CONFIG);
  accessToken: string;
  idToken: string;
  user: any;
  public initialized: Promise<any>;
  public loggedIn: boolean;
  loading = true;

  constructor(
    public zone: NgZone,
    public localStorageService: LocalStorageService,
  ) {
    this.initialized = new Promise((resolve) => {
      (async () => {
        this.user = await this.localStorageService.getItem('profile');
        this.accessToken = await this.localStorageService.getItem('access_token');
        this.idToken = await this.localStorageService.getItem('id_token');
        this.loggedIn = Date.now() < JSON.parse(await this.localStorageService.getItem('expires_at'));
        resolve();
        this.loading = false;
      })();
    });
  }

  public async getToken(): Promise<string> {
    return this.idToken;
  }

  public async handleAuthentication(): Promise<any> {
    throw new Error("Method not implemented.");
  }

  login() {
    this.loading = true;
    const options = {
      scope: 'openid profile offline_access'
    };
    // Authorize login request with Auth0: open login page and get auth results
    this.Client.authorize(options, (err, authResult) => {
      if (err) {
        throw err;
      }

      // Set Access Token
      this.localStorageService.setItem('access_token', authResult.accessToken);
      this.localStorageService.setItem('id_token', authResult.idToken);
      this.accessToken = authResult.accessToken;
      this.idToken = authResult.idToken;
      // Set Access Token expiration
      const expiresAt = JSON.stringify((authResult.expiresIn * 1000) + new Date().getTime());
      this.localStorageService.setItem('expires_at', expiresAt);

      // This is asynchronous but should be fine for now.
      this.getUser(this.idToken);

      // Set logged in
      this.loading = false;
      this.loggedIn = true;
      // Fetch user's profile info
      this.Auth0.client.userInfo(this.accessToken, (err, profile) => {
        if (err) {
          throw err;
        }
        this.localStorageService.setItem('profile', profile);
        this.zone.run(() => this.user = profile)
      });
    });
  }

  logout() {
    this.localStorageService.removeItem('profile');
    this.localStorageService.removeItem('access_token');
    this.localStorageService.removeItem('id_token');
    this.localStorageService.removeItem('expires_at');
    this.accessToken = null;
    this.user = null;
    this.loggedIn = false;
  }

  public async isAuthenticated(): Promise<boolean> {
    const expiresAt = JSON.parse(await this.localStorageService.getItem('expires_at') || '{}');
    return new Date().getTime() < expiresAt;
  }

  public async getUser(token: string): Promise<any> {
    let url = environment.apiAddress + `/api/v1/protected/auth`;
    
    let authorization = `Bearer ${token}`;
    
    const user = await restService.getProtected(url, authorization);
    this.localStorageService.setItem('username', user.twitchUsername);

    return user;
  }
}


