import { Component, ViewChild } from '@angular/core';
import { Platform, Nav, NavOptions, NavController, MenuController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { TrendingPage } from '../pages/trending/trending';
import { LocalStorageService } from './services/localStorage.service';
import { UsersService } from './services/users.service';
import { ToastService } from './services/toast.service';
import Auth0Cordova from '@auth0/cordova';
import { AbstractAuthenticationService } from './services/abstract-auth.service';
import { AuthoritiesService } from './services/authorities.service';
import { AuthorityModel } from './models/authority.model';
import { AuthoritiesModelService } from './services/authorities.model.service';

(window as any).global = window;

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild('content') navCtrl: NavController;
  @ViewChild('menu') menuCtrl: MenuController;
  rootPage: any = TrendingPage;
  public authoritiesModelService: AuthoritiesModelService;
  private authoritiesService: AuthoritiesService;
  public authService: AbstractAuthenticationService;
  private localStorageService: LocalStorageService;
  public toastService: ToastService;

  constructor(
      platform: Platform,
      statusBar: StatusBar,
      splashScreen: SplashScreen,

      /* Model Services */
      authoritiesModelService: AuthoritiesModelService,

      /* Services */
      authoritiesService: AuthoritiesService,
      authService: AbstractAuthenticationService,
      localStorageService: LocalStorageService,
      toastService: ToastService,
      usersService: UsersService) {
    this.authoritiesModelService = authoritiesModelService;
    this.authoritiesService = authoritiesService;
    this.authService = authService;
    this.localStorageService = localStorageService;
    this.toastService = toastService;
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();

      (window as any).handleOpenURL = (url) => {
        Auth0Cordova.onRedirectUri(url);
      };

      
    });
  }

  /**
   * On init.
   */
  async ngOnInit() {
    if (window.location.href.indexOf('#access_token') > -1) {
      await this.authService.handleAuthentication();
    }

    await this.authService.initialized;

    if (this.authService.loggedIn && (!this.authoritiesModelService.getAuthorityList() || !this.authoritiesModelService.getAuthorityList().length)) {
      this.authoritiesModelService.setAuthorityList(await this.authoritiesService.getList());
    }
  }

  /**
   * Logs out.
   */
  public login() {
    this.authService.login();
  }

  /**
   * Logs out.
   */
  public logout() {
    this.authService.logout();
  }

  /**
   * Goes to the given page.
   */
  goToPage(page) {
    let navOptions: NavOptions = {};
    let that = this;

    navOptions.updateUrl = true;
    this.navCtrl.setRoot(page, null, navOptions)
      .catch(err => {
        console.error(err);
        that.toastService.show('You must log in to access this page.');
      });
    this.menuCtrl.close();
  }
}



