import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { RestService } from './rest.service';
import { AbstractAuthenticationService } from './abstract-auth.service';
import { AuthorityModel } from '../models/authority.model';

@Injectable()
export class AuthoritiesService {
  constructor(
    private authService: AbstractAuthenticationService,
    private restService: RestService) {}

    /**
     * Gets the authorities list.
     */
    async getList(): Promise<Array<AuthorityModel>> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/authorities`;

      return this.restService.getProtected(url, authorization);
    }
  }


