/* Core */
import { Component, OnInit } from '@angular/core';
import { IonicPage, ViewController } from 'ionic-angular';
import { HighlightModel } from '../../app/models/highlight.model';
import { HighlightsService } from '../../app/services/highlights.service';
import { HighlightClipModel } from '../../app/models/highlightclip.model';

@IonicPage({
  name: 'addhighlightclip',
  segment: 'addhighlightclip'
})
@Component({
  selector: 'page-addhighlightclip',
  templateUrl: 'addhighlightclip.html'
})
export class AddHighlightClipPage implements OnInit {
  public description: string;
  public duration: string;
  public startTime: string;
  public selectedHighlight: HighlightModel;
  public highlights: HighlightModel[];
  public selectOptions: any;

  constructor(private highlightsService: HighlightsService,
    public viewCtrl: ViewController) {}

  async ngOnInit(): Promise<HighlightModel[]> {

    this.selectOptions = {
      title: 'Select the highlight',
      subTitle: undefined,
      mode: 'md'
    };

    this.startTime = "00";

    if (this.viewCtrl.data.duration) {
      this.duration = Math.floor(this.viewCtrl.data.duration).toString();
    }

    this.highlights = await this.highlightsService.getList();

    this.highlights = this.highlights.sort((a: HighlightModel, b: HighlightModel) => {
      if (a.createdAt < b.createdAt) {
        return 1;
      } else if (a.createdAt === b.createdAt) {
        return 0;
      } else {
        return -1;
      }
    });

    /* Most recent highlight is selected by default */
    this.selectedHighlight = this.highlights[0];

    /* Set clip title as default description */
    this.description = this.viewCtrl.data.title;

    return this.highlights;
  }

  add() {
    const highlightClip = new HighlightClipModel();

    highlightClip.description = this.description;
    highlightClip.duration = parseInt(this.duration);
    highlightClip.startTime = parseInt(this.startTime);
    highlightClip.highlightUuid = this.selectedHighlight.uuid;

    this.viewCtrl.dismiss(highlightClip);
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }
}



