import { Component, Input, Output } from '@angular/core';
import { NavController, ItemSliding } from 'ionic-angular';
import { Channel } from '../../models/channel.model';
import { CurrentChannelStoreService } from '../../services/current-channel.store.service';
import { EventEmitter } from '@angular/core';
import { AbstractAuthenticationService } from '../../services/abstract-auth.service';

@Component({
  selector: 'channel-thumbnail-component',
  templateUrl: 'channel-thumbnail.component.html'
})
export class ChannelThumbnailComponent {
  @Input() channel: Channel;
  @Input() isFavorite: boolean;
  @Output() favorite = new EventEmitter();
  @Output() unfavorite = new EventEmitter();

  constructor(private authService: AbstractAuthenticationService, private currentChannelStoreService: CurrentChannelStoreService, public navCtrl: NavController) {}

  favoriteItem(slidingItem: ItemSliding) {
    this.favorite.emit(null);
    slidingItem.close();
  }

  unfavoriteItem(slidingItem: ItemSliding) {
    this.unfavorite.emit(null);
    slidingItem.close();
  }

  goToChannel(channel) {
    this.currentChannelStoreService.setId(channel._id);
    this.currentChannelStoreService.setName(channel.channel);

    this.navCtrl.push('trending-channel', {channel: channel});
  }
}


