import { Pipe } from '@angular/core';
import * as moment from 'moment';
import { UserComment } from '../models/user-comment.model';

@Pipe({
  name: 'userCommentsSort'
})
export class UserCommentsPipe {
  transform(array: Array<UserComment>): Array<UserComment> {
    array.sort((a: UserComment, b: UserComment) => {
      let aMoment: moment.Moment = moment(a.createdAt, 'YYYY-MM-DD HH:mm:ss');
      let bMoment: moment.Moment = moment(b.createdAt, 'YYYY-MM-DD HH:mm:ss');

      return bMoment.diff(aMoment);
    });
    return array;
  }
}



