import { AuthorityModel } from "../models/authority.model";

export class AuthoritiesModelService {
    private authorityList: Array<AuthorityModel>;

   /**
   * Whether or not the user has the given authority.
   * 
   * @param nane The authority name.
   */
  public hasAuthority(name: string): boolean {
    return this.authorityList && this.authorityList.filter(authority => authority.name === name).length > 0;
  }

    public getAuthorityList(): Array<AuthorityModel> {
        return this.authorityList;
    }

    public setAuthorityList(authorityList: Array<AuthorityModel>): void {
        this.authorityList = authorityList;
    }
}


