import { Injectable } from '@angular/core';
import * as auth0 from 'auth0-js';
import { environment } from '../../../environments/environment';

import { AbstractAuthenticationService } from './abstract-auth.service';
import { RestService } from './rest.service';
import { LocalStorageService } from './localStorage.service';

(window as any).global = window;

const restService: RestService = new RestService();

@Injectable()
export class WebAuthenticationService implements AbstractAuthenticationService {
  public initialized: Promise<any>;
  public loggedIn: boolean;

  auth0 = new auth0.WebAuth({
    clientID: 'V5nVHV0bHZVyF2j6GFnONiBw2e5x6Amj',
    domain: 'newblt123.auth0.com',
    responseType: 'token id_token',
    audience: 'https://newblt123.auth0.com/userinfo',
    redirectUri: `${environment.authCallback}`,
    scope: 'openid'
  });

  constructor(private localStorageService: LocalStorageService) {
    this.initialized = new Promise((resolve) => {
      (async () => {
        this.loggedIn = Date.now() < JSON.parse(await this.localStorageService.getItem('expires_at'));
        resolve();
      })();
    });
  }

  public async getToken(): Promise<string> {
      return this.localStorageService.getItem('id_token');
  }

  public login(): void {
    this.auth0.authorize();
  }

  public async handleAuthentication(): Promise<any> {
    let that = this;

    return new Promise((resolve, reject) => {
      this.auth0.parseHash(async (err, authResult) => {
        if (authResult && authResult.accessToken && authResult.idToken) {
          window.location.hash = '';
          this.setSession(authResult);
  
          this.loggedIn = true;
  
          await that.getUser(authResult.idToken);
          resolve();
        } else if (err) {
          reject(err);
        }
      });
    });
  }

  private setSession(authResult): void {
    // Set the time that the Access Token will expire at
    const expiresAt = JSON.stringify((authResult.expiresIn * 1000) + new Date().getTime());
    this.localStorageService.setItem('access_token', authResult.accessToken);
    this.localStorageService.setItem('id_token', authResult.idToken);
    this.localStorageService.setItem('expires_at', expiresAt);
  }

  public logout(): void {
    // Remove tokens and expiry time from localStorage
    this.localStorageService.removeItem('access_token');
    this.localStorageService.removeItem('id_token');
    this.localStorageService.removeItem('expires_at');
    this.loggedIn = false;
    // Go back to the home route
  }

  public async isAuthenticated(): Promise<boolean> {
    // Check whether the current time is past the
    // Access Token's expiry time
    const expiresAt = JSON.parse(await this.localStorageService.getItem('expires_at') || '{}');
    return new Date().getTime() < expiresAt;
  }

  public async getUser(token: string): Promise<any> {
    let url = environment.apiAddress + `/api/v1/protected/auth`;
    
    let authorization = `Bearer ${token}`;
    
    const user = await restService.getProtected(url, authorization);
    this.localStorageService.setItem('username', user.twitchUsername);

    return user;
  }
}



