import { Component, Input } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Channel } from '../../models/channel.model';
import { CurrentChannelStoreService } from '../../services/current-channel.store.service';

@Component({
  selector: 'following-channel-thumbnail-component',
  templateUrl: 'following-channel-thumbnail.component.html'
})
export class FollowingChannelThumbnailComponent {
	@Input() channel: Channel;

  constructor(private currentChannelStoreService: CurrentChannelStoreService, public navCtrl: NavController) {}

  goToChannel(channel) {
    this.currentChannelStoreService.setId(channel._id);
    this.currentChannelStoreService.setName(channel.channel);

    this.navCtrl.push('trending-channel', {channel: channel});
  }
}



