/* Core */
import { Component, ChangeDetectorRef, NgZone, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { IonicPage, NavController, NavOptions, ModalController } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser';

/* Models */
import { GameWrapper } from '../../app/models/game-wrapper.model';

/* Services */
import { LoaderService } from '../../app/services/loader.service';
import { ToastService } from '../../app/services/toast.service';
import { HighlightsService } from '../../app/services/highlights.service';
import { HighlightModel } from '../../app/models/highlight.model';
import { CreateHighlightPage } from '../createhighlight/createhighlight';

@IonicPage({
  name: 'highlights',
  segment: 'highlights'
})
@Component({
  selector: 'page-highlights',
  templateUrl: 'highlights.html'
})
export class HighlightsPage implements OnInit {
  public highlights: HighlightModel[] = [];

  constructor(
    public navCtrl: NavController,
    private ngZone: NgZone,

    public modalCtrl: ModalController,

    /* Services */
    private highlightsService: HighlightsService,
    private loaderService: LoaderService,
    private toastService: ToastService) {}

  ngOnInit(): void {
    this.getHighlights();
  }

  getHighlights() {
    let that = this;
    let promise;

    promise = this.highlightsService.getList().then(function(highlights: HighlightModel[]) {
      that.ngZone.run(() => {
        highlights.forEach(function(highlight) {
          that.highlights.push(highlight);
        });
      });
    });

    that.loaderService.show(promise);
    that.toastService.onFailure(promise);
  }

  async onDismiss(data) {
    const that = this;
    if (data) {
      const promise = this.highlightsService.create(data);
      this.loaderService.show(promise);
      this.toastService.onFailure(promise);

      const highlight = await promise;

      this.ngZone.run(() => {
        that.highlights.push(highlight);
      });
    }
  }

  async create() {
    let myModal = this.modalCtrl.create(CreateHighlightPage);

    myModal.onDidDismiss(this.onDismiss.bind(this));
    myModal.present();
  }

  goToHighlight(uuid: string) {
    this.navCtrl.push('highlightclips', {highlight: uuid});
  }
}



