/* Core */
import { Component, OnInit } from '@angular/core';
import { IonicPage } from 'ionic-angular';
import { AbstractAuthenticationService } from '../../app/services/abstract-auth.service';
import { LoaderService } from '../../app/services/loader.service';
import { ToastService } from '../../app/services/toast.service';
import { FeedService } from '../../app/services/feed.service';

/* Models */

/* Services */

@IonicPage({
  name: 'feed',
  segment: 'feed'
})
@Component({
  selector: 'page-feed',
  templateUrl: 'feed.html'
})
export class FeedPage implements OnInit {

  public clips = [];

  constructor(
    private feedService: FeedService,
    private loaderService: LoaderService,
    public authService: AbstractAuthenticationService,
    private toastService: ToastService) { }

  ngOnInit(): void {
    (async () => {
      await this.getFeed();
    })()
  }

  async getFeed(): Promise<any> {
    const promise = this.feedService.getFeed();
    this.loaderService.show(promise);

    const feed = await promise;
    this.clips = feed.clips;

    this.toastService.onFailure(promise);
  }
}



