import { NgModule } from '@angular/core';
import { FavoriteClipsPipe } from './favorite-clips-sort.pipe';
import { TimeAgoPipe } from 'time-ago-pipe';
import { ViewsPipe } from './views.pipe';
import { CapitalizePipe } from './capitalize.pipe';
import { UserCommentsPipe } from './user-comments.pipe';
import { SecondsPipe } from './seconds.pipe';
import { HighlightClipsSortPipe } from './highlight-clips-sort.pipe';

@NgModule({
  declarations: [
    CapitalizePipe,
    FavoriteClipsPipe,
    HighlightClipsSortPipe,
    UserCommentsPipe,
    ViewsPipe,
    SecondsPipe,
    TimeAgoPipe
  ],
  exports: [
    CapitalizePipe,
    FavoriteClipsPipe,
    HighlightClipsSortPipe,
    UserCommentsPipe,
    ViewsPipe,
    SecondsPipe,
    TimeAgoPipe
  ],
  imports: [],
  providers: [],
})
export class PipesModule {
  static forRoot() {
      return {
          ngModule: PipesModule,
          providers: [],
      };
   }
}



