import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { RestService } from './rest.service';
import * as assert from 'assert';
import { AbstractAuthenticationService } from './abstract-auth.service';

@Injectable()
export class ChannelsService {
  constructor(
    private authService: AbstractAuthenticationService,
    private restService: RestService) {}

    /**
     * Adds the channel to favorites.
     * 
     * @param channelId The channel identifier.
     */
    async addFavorite(channelId: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/channels/${channelId}/favorite`;

      return this.restService.postProtected(url, authorization, null);
    }

    /**
     * Gets a channel by twitch identifier.
     * 
     * @param id The twitch identifier.
     */
    async getById(id: string): Promise<any> {
      let url = environment.apiAddress + `/api/v1/channels/${id}`;

      return this.restService.get(url);
    }

    /**
     * Gets the favorite channels.
     */
    async getFavorites(): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/channels/favorites`;

      return this.restService.getProtected(url, authorization);
    }

    /**
     * Gets whether or not the channel is favorited by the user.
     * 
     * @param channelId The channel identifier.
     */
    async getIsFavorite(channelId: string): Promise<boolean> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/channels/${channelId}/isfavorite`;

      return this.restService.getProtected(url, authorization);
    }

    /**
     * Removes the channel from favorites.
     * 
     * @param channelId The channel identifier.
     */
    async removeFavorite(channelId: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/channels/${channelId}/unfavorite`;

      return this.restService.postProtected(url, authorization, null);
    }

    /**
     * Searches for channels on Twitch.
     * 
     * @param query The channel query.
     */
    async search(query: string): Promise<any> {
      assert(query, 'query required.');

      let url = environment.apiAddress + `/api/v1/channels?channel=${encodeURIComponent(query)}`;

      return this.restService.get(url);
    }
  }


