import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { RestService } from './rest.service';
import { AbstractAuthenticationService } from './abstract-auth.service';
import { HighlightModel } from '../models/highlight.model';
import { HighlightClipModel } from '../models/highlightclip.model';

@Injectable()
export class HighlightsService {
  constructor(
    private authService: AbstractAuthenticationService,
    private restService: RestService) {}

    async addHighlightClip(uuid: string, highlightClip: HighlightClipModel): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/highlights/${uuid}/clips`;

      highlightClip.highlightUuid = undefined;

      return this.restService.postProtected(url, authorization, JSON.stringify(highlightClip));
    }

    async create(name: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/highlights`;

      const highlight = new HighlightModel();
      highlight.name = name;

      return this.restService.postProtected(url, authorization, JSON.stringify(highlight));
    }

    async getByUuid(uuid: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/highlights/${uuid}`;

      return this.restService.getProtected(url, authorization);
    }

    async getList(): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/highlights`;

      return this.restService.getProtected(url, authorization);
    }

    /**
     * 
     * @param uuid 
     * @param highlightClip 
     */
    async removeClipHighlight(uuid: string, highlightClipUuid: string): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/highlights/${uuid}/clips/${highlightClipUuid}`;

      console.log(url);

      return this.restService.delete(url, authorization);
    }

    async updateHighlightClip(uuid: string, highlightClip: HighlightClipModel): Promise<any> {
      let authorization = `Bearer ${await this.authService.getToken()}`;
      let url = environment.apiAddress + `/api/v1/protected/highlights/${uuid}/clips/${highlightClip.uuid}`;

      return this.restService.postProtected(url, authorization, JSON.stringify(highlightClip));
    }
  }


