import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';

import { ChannelsPage } from './Channels';
import { ComponentModule } from '../../app/components/component.module';
import { PipesModule } from '../../app/pipes/pipes.module';
import { ChannelsService } from '../../app/services/channels.service';
import { StreamsService } from '../../app/services/streams.service';

@NgModule({
  declarations: [
    ChannelsPage
  ],
  imports: [
    IonicPageModule.forChild(ChannelsPage),
    ComponentModule.forRoot(),
    PipesModule.forRoot()
  ],
  providers: [
    ChannelsService,
    StreamsService
  ],
  entryComponents: [
    ChannelsPage
  ]
})
export class ChannelsPageModule {}



