/* Core */
import { Component, OnInit } from '@angular/core';
import { IonicPage, ViewController } from 'ionic-angular';


@IonicPage({
  name: 'createhighlight',
  segment: 'createhighlight'
})
@Component({
  selector: 'page-createhighlight',
  templateUrl: 'createhighlight.html'
})
export class CreateHighlightPage implements OnInit {

  public highlightName: string;

  constructor(public viewCtrl: ViewController) {}

  ngOnInit(): void {
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  add() {
    this.viewCtrl.dismiss(this.highlightName);
  }
}



