/* Core */
import { Component, OnInit } from '@angular/core';
import { IonicPage } from 'ionic-angular';
import { ChannelsService } from '../../app/services/channels.service';
import { AbstractAuthenticationService } from '../../app/services/abstract-auth.service';
import { LoaderService } from '../../app/services/loader.service';
import { ToastService } from '../../app/services/toast.service';
import { StreamsService } from '../../app/services/streams.service';
import { Channel } from '../../app/models/channel.model';

/* Models */

/* Services */

@IonicPage({
  name: 'channels',
  segment: 'channels'
})
@Component({
  selector: 'page-channels',
  templateUrl: 'channels.html'
})
export class ChannelsPage implements OnInit {
  public channels: any[] = [];
  public favoriteChannels: any[] = [];
  public favoriteChannelsLoading = true;
  public liveStreams: any[] = [];
  public searchValue: string;

  constructor(
    private channelsService: ChannelsService,
    private loaderService: LoaderService,
    public authService: AbstractAuthenticationService,
    private streamsService: StreamsService,
    private toastService: ToastService) { }

  ngOnInit(): void {
    this.getFavorites();
    this.getLiveStreams();
  }

  private async getLiveStreams() {
    const liveStreams = await this.streamsService.getLiveStreams();
    this.liveStreams = liveStreams.streams;
  }

  private async getFavorites() {
    let promise = null;

    this.favoriteChannelsLoading = true;

    if (this.authService.loggedIn) {
      promise = this.channelsService.getFavorites();

      const channels = await promise;
      const promises = [];
      
      channels.forEach(channel => {
        promises.push(this.getChannel(channel));
      });

      Promise.all(promises).then(() => {
        this.favoriteChannelsLoading = false;
      });

    } else {
      promise = Promise.resolve();
    }

    this.toastService.onFailure(promise);
    this.loaderService.show(promise);
  }

  private async getChannel(channel: any) {
    const promise = this.channelsService.getById(channel.channelId);
    const twitchChannel = await promise;

    this.favoriteChannels.push(twitchChannel);

    return twitchChannel
  }

  public favorite(channel: any) {
    (async () => {
      const promise = this.channelsService.addFavorite(channel._id);
      await promise;

      this.toastService.onFailure(promise);
      this.toastService.show(`Added channel to favorites.`);
      this.favoriteChannels.push(channel);
    })();
  }

  public unfavorite(channel) {
    (async () => {
      const promise = this.channelsService.removeFavorite(channel._id);
      await promise;

      this.toastService.onFailure(promise);
      this.toastService.show(`Removed channel from favorites.`);

      const index = this.favoriteChannels.findIndex(el => el._id === channel._id);
      this.favoriteChannels.splice(index, 1);
    })();
  }

  public onInput() {
    if (this.searchValue) {
      const promise = this.channelsService.search(this.searchValue);
      this.toastService.onFailure(promise);

      promise.then(channels => {
        this.channels = channels.channels;
      });
    } else {
      this.channels = [];
    }
  }

  public onCancel(): void {

  }

  public isInFavorites(channel: Channel): boolean {
    return !!this.favoriteChannels.find(favoriteChannel => {
      return channel._id == favoriteChannel._id;
    });
  }

  public async refresh(refresher: any) {
    this.favoriteChannels = [];
    this.liveStreams = [];
    await this.getFavorites();
    await this.getLiveStreams();
    refresher.complete();
  }
}



